  text-decoration: none;
  color: rgb(255, 247, 247);
  padding-bottom: 10px;
  margin-left: 10%;
  transition: 0.1s;
}

.navigation__link:hover {
  color: #ff4c51;
}

.active-link {
  color: rgb(232, 177, 18);
}

.friends__sign {
  text-align: center;
}

.friends-list {
  display: flex;
  flex-direction: column;
  color: white;
  margin: 0 auto;
}

.friend-description__ava {
  height: 80px;
  width: 80px;
  border-radius: 50%;
}














.dialogs {
  display: grid;
  grid-template-columns: 3fr 7fr;
  flex-direction: row;
  padding: 20px 0;
}

.dialogs__items-list {
  margin-right: 10px;
}





.dialogs__item {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  color: white;
  height: 30px;
  padding: 15px 0;
  font-style: oblique;
  font-weight: bolder;
}

.dialogs__item--active {
  background-color: #ff4c51;
}
.dialog__conv {
  color: white;
  margin-bottom: 20px;
}

.dialog__input {
    resize: none;
    padding: 10px;
    font-size: 19px;
    outline: none;
    background-color: rgb(134, 87, 87);
    border: 0;
    color: white;
    height: 100px;
    width: 70%;
    margin: 20px 0 0;
}

.btn__new-message {
    display: block;
    background-color: rgb(177, 94, 94);
    font-weight: 600;
    color: white;
    transition: 0.2s;
    width: 73%;
    height: 40px;
}

.btn__new-message:hover {
    background-color: #d46d19;
}

.btn__new-message:active {
    background-color: #20d419;
}
.users-list {
    margin: 10px 0;
}

.user {
    display: flex;
    margin-bottom: 10px;
}

.user__avatar-container {
    position: relative;
    width: 200px;
    height: 210px;
}

.user__avatar {
    width: 100%;
    height: 100%;
}

.user__unfollow-btn,
.user__follow-btn,
.user__unfollow-btn-disable,
.user__follow-btn-disable {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 40px;
    opacity: 0.7;
    background-color: transparent;
    text-align: center;
    color: #ff4c51;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    transition: 0.3s;
    font-weight: bolder;
    font-size: 20px;
    cursor: pointer;
}

.user__unfollow-btn-disable,
.user__follow-btn-disable {
    background-color: yellow;
    opacity: 1;
    color: #e2001c;
    pointer-events: none;
}

.user__follow-btn {
    color: lightgreen;
}

.user__avatar-container:hover .user__unfollow-btn,
.user__avatar-container:hover .user__follow-btn {
    opacity: 1;
}

.user__unfollow-btn:active {
    background-color: #ff4c51;
    color: white;
}

.user__follow-btn:active {
    background-color: lightgreen;
    color: white;
}

.user__description {
    margin-left: 10px;
}

.user__description-item {
    margin: 5px 0;
}



.pagination__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 10px 0 20px;
}

.pagination__item,
.pagination__item-active {
    width: 40px;
    height: 40px;
    background-color: rgb(121, 118, 118);
    margin: 5px 10px 0 0;
    text-align: center;
    cursor: pointer;
    border: 0;
    font-weight: bolder;
}

.pagination__item:hover,
.pagination__list-swipe-btn--disable:hover,
.pagination__list-swipe-btn:hover {
    background-color: #ff4c51;
}

.pagination__item-active {
    background-color: rgb(232, 177, 18);
}

.pagination__list-wrapper {
    width: 500px;
    margin-left: 10px;
}

.pagination__list-swipe-btn--disable,
.pagination__list-swipe-btn {
    text-transform: uppercase;
    height: 40px;
    width: 60px;
    border: 0;
    background-color: rgb(121, 118, 118);
    font-weight: bolder;
    margin-top: 5px ;
}

.pagination__list-swipe-btn--disable {
    background-color: black;
}
.animation {
    position: absolute;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.cssload-square {
    width: 5px;
    height: 29px;
    background: lightgray;
    border-radius: 10px;
    display: block;
    animation: cssload-turn 2s ease infinite;
    -o-animation: cssload-turn 2s ease infinite;
    -ms-animation: cssload-turn 2s ease infinite;
    -webkit-animation: cssload-turn 2s ease infinite;
    -moz-animation: cssload-turn 2s ease infinite;
}

.cssload-top {
    position: absolute;
    left: 40%;
    top: 50%;
    transform: rotate(90deg);
    -o-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    -webkit-transform: rotate(90deg);
    -moz-transform: rotate(90deg);
}

.cssload-bottom {
    position: absolute;
    left: 40%;
    top: 50%;
    transform: rotate(-90deg);
    -o-transform: rotate(-90deg);
    -ms-transform: rotate(-90deg);
    -webkit-transform: rotate(-90deg);
    -moz-transform: rotate(-90deg);
}

.cssload-left {
    position: absolute;
    left: 40%;
    top: 50%;
}

.cssload-right {
    position: absolute;
    left: 40%;
    top: 50%;
    transform: rotate(-180deg);
    -o-transform: rotate(-180deg);
    -ms-transform: rotate(-180deg);
    -webkit-transform: rotate(-180deg);
    -moz-transform: rotate(-180deg);
}

@keyframes cssload-turn {
    0% {
        transform: translateX(0) translateY(0) rotate(0);
    }
    50% {
        transform: translateX(400%) translateY(100%) rotate(90deg);
    }
    100% {
        transform: translateX(0) translateY(0) rotate(0);
    }
}

@-webkit-keyframes cssload-turn {
    0% {
        -webkit-transform: translateX(0) translateY(0) rotate(0);
    }
    50% {
        -webkit-transform: translateX(400%) translateY(100%) rotate(90deg);
    }
    100% {
        -webkit-transform: translateX(0) translateY(0) rotate(0);
    }
}

.form {
    display: block;
}

.wrapper {
    display: flex;
    justify-content: space-between;
}

.inner {
    display: flex;
    align-items: center;
    height: 40px;
    box-sizing: border-box;
    padding: 10px;
}

.input {
    border: none;
    outline: none;
    cursor: pointer;
    background: transparent;
    font-size: 16px;
    width: 50px;
    transition: width 0.3s ease;
}

.search_img {
    margin-right: 5px;
    padding: 0;
    display: flex;
    align-items: center;
}

.form:focus-within {
    background: rgba(0,0,0,0.05);
    border-bottom: 2px solid gray;
}

button svg path {
    fill: #757575;
}

.clear:focus,
.clear:hover,
.close:focus svg path,
.close:hover svg path {
    fill: #424242;
    color: #424242;
    transition: all 0.3s ease;
}

.input:focus {
    width: 200px;
    cursor: auto;
    cursor: initial;
}

.bulkhead {
    width: 1px;
    height: 100%;
    background: #757575;
    margin: 0 5px;
    display: none;
}

.form:focus-within .clear,
.form:focus-within .close,
.form:focus-within .bulkhead {
    display: block;
}
